@charset "UTF-8";
h1, h2, h3, h4, h5, h6, p, ul, dl, li, body {
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
}

fieldset {
  margin: 0;
  padding: 0;
  border: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/lato-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/lato-regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswald-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswald-regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswald-medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswald-medium.woff2") format("woff2");
  font-display: swap;
}
.title {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
}

.green-button {
  display: inline-block;
  width: calc(100% - 40px);
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-size: 14px;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  background-color: #68B738;
  padding-top: 11px;
  padding-bottom: 11px;
}

.catalog-link {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
  text-transform: uppercase;
  color: #000000;
  text-decoration: none;
  padding-right: 47px;
  background: url("../img/icons/icon-arrow.png") 100% 65%/32px 11px no-repeat;
}

.page-body {
  min-width: 320px;
  font-family: "Lato", sans-serif;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  font-style: normal;
  color: #444444;
}

.main-header {
  display: flex;
  align-items: center;
  min-width: 320px;
  height: 65px;
  margin: 0 auto;
}

.main-header__nav {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  width: 280px;
  background: url("../img/logo-mobile-text.svg") center/101px 18px no-repeat;
}

.main-header__toggle {
  align-self: center;
  width: 24px;
  height: 16px;
  border: none;
  cursor: pointer;
  background: url("../img/icons/burger.png") center/24px 16px no-repeat;
}

.main-header__nav-list {
  display: none;
}

.welcome {
  width: 320px;
  height: 500px;
  margin: 0 auto;
  text-align: center;
  color: #ffffff;
  background: url("../img/bg-welcome-can.png") center 167px/280px 270px no-repeat, linear-gradient(to bottom, rgba(104, 183, 56, 0.85) 302px, #ffffff 302px, #ffffff 480px), url("../img/bg-welcome-cat.jpg") center top/320px 302px no-repeat;
}
.welcome__title {
  padding-top: 27px;
  margin-bottom: 25px;
}
.welcome__text {
  font-family: "Oswald", sans-serif;
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  text-transform: uppercase;
  margin-bottom: 302px;
}

.programs {
  width: 280px;
  margin: 0 auto 22.5px;
}
.programs__list {
  display: flex;
  flex-wrap: wrap;
  gap: 21px;
}
.programs__item {
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 20px;
  background: url("../img/cat-sitting.svg") 21.5px 21px/50px 53px no-repeat, #f2f2f2;
}
.programs__item:nth-of-type(2) {
  background: url("../img/cat-walking.svg") 12.5px 24px/67px 50px no-repeat, #f2f2f2;
}
.programs__item-title {
  font-size: 24px;
  line-height: 26px;
  text-transform: uppercase;
  color: #000000;
  padding-top: 35px;
  padding-left: 70px;
  margin-bottom: 34px;
}
.programs__item-text {
  font-size: 14px;
}
.programs__item-line {
  border-top: 1px solid #d9d9d9;
  margin-top: 23px;
}
.programs__item-link {
  display: inline-block;
  margin-top: 10px;
  margin-left: -1px;
  letter-spacing: 0.1px;
}

.details {
  width: 280px;
  margin: 0 auto 50px;
}
.details__title {
  color: #000000;
  margin-bottom: 42px;
}
.details__list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}
.details__item {
  height: 60px;
  background-position: top left -1px;
}
.details__item:nth-of-type(1) {
  background: url("../img/icons/details-1.jpg") top left -1px/61px 60px no-repeat;
}
.details__item:nth-of-type(2) {
  background: url("../img/icons/details-2.jpg") top left -1px/61px 60px no-repeat;
}
.details__item:nth-of-type(3) {
  background: url("../img/icons/details-3.jpg") top left -1px/61px 60px no-repeat;
}
.details__item:nth-of-type(4) {
  background: url("../img/icons/details-4.jpg") top left -1px/61px 60px no-repeat;
}
.details__item-text {
  font-size: 14px;
  padding-left: 80px;
  margin-top: 1.5px;
}

.example {
  width: 320px;
  margin: 0 auto;
  padding-top: 26px;
  background-color: #EAEAEA;
}
.example__container {
  width: 280px;
  margin: 0 auto;
}
.example__title {
  margin-bottom: 41.5px;
}
.example__description {
  font-size: 14px;
  margin-bottom: 20px;
}
.example__list {
  display: flex;
  flex-wrap: wrap;
  gap: 32px;
  margin-bottom: 35px;
  text-align: center;
}
.example__item {
  position: relative;
  width: 122px;
  display: flex;
  flex-direction: column-reverse;
  border: 1px solid #CDCDCD;
  border-radius: 3px;
  padding-top: 15px;
  padding-bottom: 10px;
}
.example__item-title {
  position: absolute;
  left: 0;
  right: 0;
  bottom: -15px;
  margin: 0 auto;
  width: 77px;
  font-size: 12px;
  line-height: 12px;
  background-color: #EAEAEA;
}
.example__item-text {
  font-family: "Oswald", sans-serif;
  font-size: 24px;
  line-height: 24px;
  letter-spacing: -0.05px;
  color: #000000;
  margin: 0 0 6.5px;
}
.example__total {
  font-family: "Oswald", sans-serif;
  font-size: 14px;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 20px;
}
.example__preview {
  margin-bottom: 39px;
}

.contacts {
  width: 320px;
  margin: 0 auto;
}
.contacts__container {
  width: 280px;
  margin: 0 auto 26px;
  padding-top: 25px;
  display: flex;
  justify-content: space-between;
}
.contacts__title {
  width: 139px;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: #111111;
}
.contacts__address {
  width: 139px;
  font-style: normal;
  font-size: 14px;
  line-height: 20px;
}

.main-footer {
  width: 320px;
  margin: 0 auto;
  margin-top: -3px; /* закрывает белую линию на картинке сверху */
  background-color: #f2f2f2;
}